import React, { useEffect, useState } from 'react'
import {Card, Col, Row, Button, Input, Dropdown} from 'antd'
import { Api } from '../api/api'
import { Link } from 'react-router-dom'


function History() {
    const [ history, setHistory ] = useState({})

    useEffect(() => {
        Api.get('/seller/history').then(r => setHistory(r.data))
        console.log(history)
    }, [])


    return (
        <>
            <Card
                title={<b></b>}
                extra={<Button onClick={() => window.history.back()}></Button>}
            >
                <Card title="">


                    { history?.transactions ? history.transactions.map(i =>

                        <Card
                            extra={` : ${i.date_check}`}
                            style={{ marginBottom: 10}}
                            title={` : ${i.unique_inv}`}>

                            <Col style={{fontSize: 16}}> : {i.category_name}</Col>
                            <Row gutter={13}>
                                <Col span={8}>
                                    <p style={{fontSize: 16}}> : {i.subcategory_name}</p>
                                    <p style={{fontSize: 16}}>  : {i.unique_code}</p>
                                    <p style={{fontSize: 16}}> : {i.amount} </p>
                                    <p style={{fontSize: 16}}>   : {i.created_at.split('.')[0]}</p>
                                </Col>
                                <Col span={8}>
                                    <p style={{fontSize: 16,
                                        display: "inline-block",boxSizing: "content-box",
                                        border: "2px solid",borderColor: "lightgreen",
                                        borderRadius: "15px", padding: "20px 30px", marginLeft: 50}}> : {i.content_key.split('\n').map(k => <div >{k}</div>)}</p>
                                </Col>
                                <Col span={8} >
                                    <Button type={"dashed"} style={{ marginLeft: 50}}>
                                        <Link to={`/history/transaction/${i.id}`}>
                                            
                                        </Link>
                                    </Button>
                                </Col>
                            </Row>

                            <Row>

                            </Row>

                        </Card>) : <></> }
                </Card>
            </Card>
        </>
    )
}

export default History
